body {
  background-color: #222;
  color: #fff;
  font-family: Roboto, sans-serif;
}

.controls {
  margin-bottom: 10px;
}

.text-input, .font-size-input {
  padding: 5px;
  font-size: 14px;
  background-color: #333;
  color: #fff;
  border: none;
}

.text-input {
  width: 98%;
  height: 200px;
}

.text-container {
  padding-top: 10px;

  background-color: #333;
  line-height: 1.5;
}

p {
  margin-bottom: 10px;
  padding-left: 30%;
  padding-right: 40%; 
}

.focused {
  background-color: rgba(34, 34, 34, 0.8);
}

.progress-bar {
  width: 100%;
  height: 30px;
  background-color: #444;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}

.font-size-input {
  width: 60px;
}

.label {
  display: block;
  margin-bottom: 5px;
  font-size: 16px;
}

.progress-container, .reading-progress-container {
  position: fixed;
  width: 100%;
  background-color: #e0e0e0;
}

.progress-container {
  top: 0;
  left: 0;
  height: 2%;
}

.reading-progress-container {
  position: relative;
  width: 100%;
  height: 40px;
  background-color: #e0e0e0;
}

.reading-progress {
  position: absolute;
  left: 0;
  height: 100%;
  background-color: #6200ee;
  transition: width 0.3s ease;
}

.reading-progress-percentage {
  position: absolute;
  width: 100%;
  text-align: center;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000;
  margin-left: 30px;
}
